* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: #eee;
  text-decoration: underline;
}

body {
  background-color: #1f2430;
  padding: 4rem;
  font-family: 'Montserrat', sans-serif;
}

li {
  list-style-type: none;
}

.App {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}

.user-media {
  display: flex;
  flex-direction: row;
}

.media-switchers {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.switcher {
  width: 60px;
  height: 60px;
  border-radius: 50%;

  border: none;
  outline: none;

  margin: 1rem 1rem;

  cursor: pointer;
  font-size: 1.3em;
  color: #fff;
  background: rgb(187, 34, 34);

  transition: 0.3s;

  box-shadow: 3px 3px 5px #16181f;
}

.on {
  background: rgb(34, 187, 34);
}

.contacts-list {
  padding: 2rem;
}

.contactBtn {
  padding: 1rem 2rem;
  border: none;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.2em;
  border-radius: 5px;
  color: #eee;
  background-color: #232731;
  margin: 2rem 0;
  margin-right: 2rem;
  box-shadow: 3px 3px 5px #16181f;

  transition: 0.3s;
}

.contactBtn:hover {
  background-color: #232731;
}

.contactBtn:active {
  transform: scale(0.9);
}

.phone-icon {
  color: rgb(31, 204, 54);
  margin-right: 15px;
}

.chatInput {
  margin-bottom: 2rem;
}

.messageTxt {
  background: #232731;
  font-size: 1.3rem;
  padding: 1rem 2rem;
  border: none;
  outline: none;
  color: #eee;
  width: 800px;
  box-shadow: 3px 3px 5px #16181f;

  border-radius: 5px;
}

.messageTxt:focus {
  border: none;
  outline: none;
}

.messageSendBtn {
  margin-left: 1rem;
  background: rgb(18, 143, 226);
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1.5rem;
  color: #eee;
  box-shadow: 3px 3px 5px #16181f;
  transition: 0.3s;
  border-radius: 5px;
  padding: 1rem 4rem;
}

.messageSendBtn:active {
  transform: scale(0.9);
}

.message-body {
  padding: 1rem;
  display: flex;
}

.message-sender {
  display: flex;
  flex-direction: row;

  padding-top: 0.4rem;
}

.message-sender-ava {
  background-color: #171a22;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  text-align: center;
  color: #eee;
  font-size: 1.4em;

  line-height: 50px;
}

.message-sender-name {
  color: #eee;
  line-height: 50px;
  margin-left: 1rem;
}

.message-content {
  color: #eee;
  padding: 1rem 2rem;
  background: #232731;
  max-width: 80%;
  border-radius: 30px;
  position: relative;
  margin-left: 3rem;
  line-height: 30px;
  box-shadow: 3px 3px 5px #16181f;
}

.message-content::before {
  position: absolute;
  display: block;
  content: '...';
  left: -30px;
  top: 6px;
  font-size: 2rem;
}
